.dialogs{
    display: grid;
    column-gap: 10px;
    grid-template-columns: 200px auto;
    grid-template-areas: "sidebar wrapper";
}
.sidebar{
    height: 75vh;
    grid-area: sidebar;
    overflow-y: auto;
}
.wrapper{
    padding-left: 10px;
    grid-area: wrapper;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: stretch;
}
.noContent{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
    background-color: #1d1d1d;
    height: 65vh;
    color: white;
}